import { Component, OnInit } from '@angular/core';
import { TasksService } from '../../../services/tasks';
declare var $: any;

@Component({
    selector: 'todo',
    templateUrl: 'src/components/admin/tasks/index.html',
    providers: [ TasksService ]
})

export class Tasks implements OnInit {
    tasks: any[];
    taskSelected: any = {};
    constructor(private tasksService: TasksService) {}

    ngOnInit() {
        this.getTasks();
    }

    getTasks() {
        this.tasksService.getTasks()
            .then((tasks) => { this.tasks = tasks; });
    }

    autoSave() {
        if (this.taskSelected && this.taskSelected._id) {
            this.updateTask();
        } else {
            this.createTask();
        }
    }

    createTask() {
        let self = this;
        self.tasksService.createTask(self.taskSelected)
            .then((task) => { self.taskSelected = task; });
    }

    updateTask() {
        let self = this;
        self.tasksService.updateTask(self.taskSelected)
            .then((task) => { self.taskSelected = task; });
    }
}
